import { Message } from '@arco-design/web-react'
import axios from 'axios'
// let basePath = 'http://192.168.0.105:3000'
let basePath = 'http://101.43.216.253:3000'

// let basePath = ''
// let servicePath = ''
let instance = axios.create({
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    }
  })
  // http request 
instance.interceptors.request.use(
    config => {
      const token = localStorage.getItem('token')
      if (token ) { // tokenhttp headertoken
        config.headers.authorization = token  //token
      }
      return config
    },
    err => {
      return Promise.reject(err)
    })
// http response 
instance.interceptors.response.use(
    response => {
      // 
      // console.log(response)
      if (response.data.code) {
        switch (response.data.code) {
          case 200:
            // Toast.show('')
            break
          case 400:
            Message.error(response.data.message || response.data.msg || '')
            break
          case 801:

            break
          case 802:

            break
          case  803:

            break
          case  800:

            break
          default:
            // Toast.show(response.data.message)
            // Message.error(response.data.message || response.data.msg || '')
            break
        }
      }
      return response
    },
)
let http = {
    get: async (path, params, cookie = false)=>{
        params.timestamp = new Date().getTime()
        if(!cookie)
          params.cookie = encodeURIComponent(localStorage.getItem('cookie'))
        return await instance.get(basePath + path, {
            params,
            withCredentials: true,
        })
    },
    post: async (path, params, cookie = false)=>{
        params.timestamp = new Date().getTime()
        if(!cookie)
          params.cookie = encodeURIComponent(localStorage.getItem('cookie'))
        return await instance.post(basePath + path, {
            params,
            withCredentials: true,
        }) 
    }
}

